import React from 'react';

import Api from 'api';
import classNames from 'classnames';
import styles from "./reservations-list.scss";
import * as Tool from 'tool';
import { inject, observer } from 'mobx-react';

import LoadingMore from 'components/base/loading-more';
import Loading from 'components/base/loading-inner';
import GoTop from 'components/base/go-top';
import ContactWay from 'components/business/contact-way';
import LazyLoad from 'react-lazy-load';
import Confirm from 'components/base/confirm';

import * as Common from 'tool/common';
import * as getUserIdInfo from 'tool/getUserId';

@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class ReservationsList extends React.Component {
  constructor (props){
    super(props);
    this.state = {
      goTop: false,
      isContactWayWrapperShow: false,
      currentTab: 0, // 当前tab项 0: 全部，1: 待服务，2: 服务中，3: 已完成，4: 已取消
      reservationsList: [], // 我的订单列表
      pageIndex: 1,
      getMoreFlag: true,
      imUrl: '', //联系客服IM地址
      phoneNum: [], // 电话
      isLoadMoreShow: true,
      loderMoreText: '加载中...',
      pageRender: false,
      currentItem: {}, // 当前点击的列表项
      currentIndex: 0,
      confirm: {
        show: false,
        leftBtnText: '取消',
        rightBtnText: '确定',
        title: '确认取消订单吗',
        content: '',
      },
      isLoading: false,
      fromTabChange: false // tab切换触发滚动事件节流
    };
  }

  componentDidMount() {
    this.props.ComNoContent.$comConntent('');
    this.props.Header.$setTitle('我的预约单');
    Tool.setTitle('我的预约单');
    this.setState({
      isLoading: true
    });
    let { userId } = getUserIdInfo.getUserId();
    Common.trackCustomPageInit({
      'site_id': 1211120,
      'category_id': 1595,
      'sub_category_id': 18474
    }, {
      'userId': userId
    });
    // 请求列表
    this.getReservationsListHandel();

    // 绑定滚动事件
    window.addEventListener('scroll', () => {
      this.setState({
        goTop: document.documentElement.scrollTop > 400 || document.body.scrollTop > 400
      });
      // 加载更多
      if ((Tool.getScrollHeight() <= Tool.getWindowHeight() + Tool.getDocumentTop() + 30) && this.state.getMoreFlag && !this.state.fromTabChange) {
        this.getReservationsMoreList();
      }
    }, false);
  }
  // methods

  // 点击统计事件
  handleTrackCustomEvent(action, objectId) {
    let { userId } = getUserIdInfo.getUserId();
    // eslint-disable-next-line
    trackCustomEvent('yc_page_common_click', {
      'bus': '1',
      'refpage': Tool.getCookie('refpage'),
      'channel': Tool.getCookie('channel'),
      'city': Tool.getCookie('cityId') || 110100,
      'action': action
    }, {
      'userId': userId,
      'objectId': objectId
    });
  }

  // 获取订单列表
  getReservationsListHandel() {
    this.setState({
      isLoadMoreShow: false
    });
    this.props.ComNoContent.$comConntent('');
    let params = {
      pageIndex: this.state.pageIndex,
      pageSize: 15,
      type: this.state.currentTab
    };
    Api.getReservationsList(params).then((res) => {
      this.setState({
        pageRender: true,
        isLoading: false
      });
      if (res.returncode === 0) {
        this.setState({
          isLoadMoreShow: false
        });
        if (res.result && res.result.list && res.result.list.length) {
          let resultList = res.result.list;
          if (resultList.length < 15) {
            this.setState({
              isLoadMoreShow: true,
              loderMoreText: '加载完成',
              getMoreFlag: false
            });
          }
          this.setState({
            reservationsList: resultList
          });
        } else if (res.result && res.result.list && !res.result.list.length) {
          this.setState({
            getMoreFlag: false,
            isLoadMoreShow: false
          });
          this.props.ComNoContent.$comConntent('暂无数据');
        }
      } else if (res.returncode === 501) { // 未登录
        Common.handleLogin({
          backUrl: encodeURIComponent(window.location.href),
          queryParams: {},
          successCb: () => {
            window.location.reload();
          },
          failCb: () => {
          }
        });
        this.setState({
          reservationsList: [],
          pageRender: false
        });
        this.props.ComNoContent.$comConntent(res.message);
      } else {
        this.setState({
          reservationsList: [],
          pageRender: false
        });
        this.props.ComNoContent.$comConntent(res.message || '页面溜走了，请稍候重试');
      }
    }).catch(() => {
      this.setState({
        reservationsList: [],
        pageRender: false,
        isLoading: false
      });
      this.props.ComNoContent.$comConntent('页面溜走了，请稍候重试');
    });
  }

  // 加载更多
  getReservationsMoreList() {
    this.props.ComNoContent.$comConntent('');
    this.setState({
      isLoadMoreShow: true,
      loderMoreText: '加载中...'
    });
    this.setState({
      pageIndex: this.state.pageIndex + 1
    }, () => {
      let params = {
        pageIndex: this.state.pageIndex,
        pageSize: 15,
        type: this.state.currentTab
      };
      Api.getReservationsList(params).then((res) => {
        if (res.returncode === 0) {
          if (res.result && res.result.list && res.result.list.length) {
            let resultList = res.result.list;
            this.setState({
              reservationsList: this.state.reservationsList.concat(resultList)
            });
          } else if (res.result && res.result.list && !res.result.list.length) {
            this.setState({
              getMoreFlag: false,
              isLoadMoreShow: true,
              loderMoreText: '加载完成'
            });
          }
        } else {
          this.setState({
            reservationsList: [],
            getMoreFlag: false
          });
          this.props.ComNoContent.$comConntent(res.messgae || '页面溜走了，请稍候重试');
        }
      }).catch(() => {
        this.setState({
          reservationsList: [],
          getMoreFlag: false,
          isLoading: false
        });
        this.props.ComNoContent.$comConntent('页面溜走了，请稍候重试');
      });
    });
  }

  // 查看訂單詳情
  linkOrderDetail(e, item, type) {
    let action = type === 1 ? 'tmfy_order_list_order_item_click' : 'tmfy_order_list_detail_click';
    this.handleTrackCustomEvent(action, item.orderId);

    e.stopPropagation();
    Common.pageJumpHandel('reservationsDetail', {
      orderId: item.orderId
    });
  }

  // 查看进程
  processBtnHandel(e, item, type) {
    let action = type === 1 ? 'tmfy_order_list_new_status_click' : 'tmfy_order_list_progress_click';
    this.handleTrackCustomEvent(action, item.orderId);

    if (item.progressCode === 30) {
      return;
    }
    e.stopPropagation();
    Common.pageJumpHandel('processTracking', {
      orderId: item.orderId
    });
  }

  // 取消预约
  cancelReservationHandel(e, item, index) {
    e.stopPropagation();
    this.handleTrackCustomEvent('tmfy_order_list_cancel_click', item.orderId);

    this.setState({
      confirm: Object.assign({}, this.state.confirm, {show: true}),
      currentItem: item,
      currentIndex: index
    });
  }

  // confirm关闭弹窗
  onConfirmClose() {
    this.setState({
      confirm: Object.assign({}, this.state.confirm, {show: false})
    });
  }

  // confirm弹窗左按钮
  leftBtnAction() {
    this.onConfirmClose();
  }

  // confirm弹窗右按钮
  rightBtnAction() {
    this.setState({
      confirm: Object.assign({}, this.state.confirm, {show: false})
    }, () => {
      let params = {
        orderId: this.state.currentItem.orderId
      };
      Api.getCancelOrder(params).then((res) => {
        if (res.returncode === 0) {
          this.state.reservationsList[this.state.currentIndex] = Object.assign({}, this.state.reservationsList[this.state.currentIndex], {orderState: 4, orderStateDesc: '已取消'});
          this.setState({
            reservationsList: this.state.reservationsList
          });
        } else {
          this.props.Toast.$toast(res.message);
        }
      }).catch((error) => {
        this.setState({
          isLoading: false
        });
        this.props.Toast.$toast(error.message || '页面溜走了，请稍后再试');
      });
    });
  }

  // tab切换
  tabChangeHandel(e, type) {
    if (e.target.classList[0] && e.target.classList[0].indexOf('active') !== -1) {
      return;
    }
    this.setState({
      getMoreFlag: true,
      currentTab: type,
      pageIndex: 1,
      reservationsList: [],
      isLoading: true,
      fromTabChange: true
    }, () => {
      this.getReservationsListHandel();
      // 统计
      this.handleTrackCustomEvent('tmfy_order_list_status_tab_click', type);
    });
    // 处理tab切换会触发滚动事件请求两页数据
    setTimeout(() => {
      this.setState({
        fromTabChange: false
      });
    }, 60);
  }

  // 联系客服
  contactServiceHandel(e, item) {
    this.handleTrackCustomEvent('tmfy_order_list_contact_im_click', item.orderId);

    e.stopPropagation();
    if (item.dealerPhone && item.dealerPhone.length && !item.imUrl) { // 只有电话-直接拨打电话
      // window.location.href = `tel:${item.dealerPhone[0]}`;
      require('jsBridge');
      window.jsBridgeV2.tel({
        tel: JSON.stringify(item.dealerPhone[0]),
        success: (res) => {
          console.log('拨打电话成功', res);
        },
        fail: (err) => {
          console.log('拨打电话失败', err);
        }
      });
      return;
    }
    if (item.imUrl && item.dealerPhone && !item.dealerPhone.length) { // 只有IM地址-直接跳转Im地址
      Common.pageJumpHandel(`${item.imUrl}&pvareaid=6838752`);
      return;
    }
    this.setState({
      isContactWayWrapperShow: true
    }, () => {
      this.setState({
        imUrl: `${item.imUrl}&pvareaid=6838752`,
        phoneNum: item.dealerPhone
      });
    });
  }

  // 关闭联系客服弹窗
  contactWayClose() {
    this.setState({
      isContactWayWrapperShow: false
    });
  }

  render() {
    const {
      pageRender,
      goTop,
      currentTab,
      confirm,
      isContactWayWrapperShow,
      reservationsList,
      imUrl,
      phoneNum,
      isLoading
    } = this.state;
    return (
      pageRender ? (
      <div className={styles.my_reservation_container}>
        <ul className={styles.tab_wrapper}>
          <li onClick={(e) => this.tabChangeHandel(e, 0)} className={classNames({[styles.active]: currentTab === 0})}>全部</li>
          <li onClick={(e) => this.tabChangeHandel(e, 1)} className={classNames({[styles.active]: currentTab === 1})}>待服务</li>
          <li onClick={(e) => this.tabChangeHandel(e, 2)} className={classNames({[styles.active]: currentTab === 2})}>服务中</li>
          <li onClick={(e) => this.tabChangeHandel(e, 3)} className={classNames({[styles.active]: currentTab === 3})}>已完成</li>
          <li onClick={(e) => this.tabChangeHandel(e, 4)} className={classNames({[styles.active]: currentTab === 4})}>已取消</li>
        </ul>
        <ul className={styles.reservation_list_wrapper}>
          {
            reservationsList && reservationsList.length > 0 && reservationsList.map((item, index) => {
              return (
                <li key={item.orderId || index} onClick={(e) => this.linkOrderDetail(e, item, 1)}>
                  <div className={styles.list_hd}>
                    {!item.dealerType && <p className={styles.dealer_type_lavel}>4S店</p>}
                    <p className={styles.dealer_name}>{ item.dealerName }</p>
                    <p className={classNames(styles.reservation_state, {[styles.finished_state]: item.orderState === 3, [styles.cancel_state]: item.orderState === 4})}>{ item.orderStateDesc }</p>
                  </div>
                  <div className={styles.list_bd}>
                    <div className={styles.dealer_img_wrapper}>
                      <LazyLoad width={110} height={84}>
                        <img src={item.dealerImg} alt=""/>
                      </LazyLoad>
                      { item.transparentScore ? <span className={styles.blue_label}></span> : '' }
                      { item.transparentScore ? <span className={styles.transparency_label}>{ `透明度${item.transparentScore}` }</span> : '' }
                    </div>
                    <div className={styles.list_bd_right}>
                      <div className={styles.appointment_time}>
                        <p>预约到店时间:</p>
                        <p>{ item.appointTime }</p>
                      </div>
                      <div className={styles.btn_wrapper}>
                        {item.orderState ===1 && <p className={styles.process_btn} onClick={(e) => this.cancelReservationHandel(e, item, index)}>取消预约</p>}
                        {(item.orderState === 2 && item.progressCode !== 30) && <p className={styles.process_btn} onClick={(e) => this.processBtnHandel(e, item, 1)}>查看进程</p>}
                        {(item.orderState !== 4 && item.orderState !== 3 && (item.imUrl || (item.dealerPhone && item.dealerPhone.length))) && <p className={styles.service_btn} onClick={(e) => this.contactServiceHandel(e, item)}>{
                          item.imUrl ? '联系客服' : '拨打电话'
                        }</p>}
                        {(item.orderState === 4 || item.orderState === 3) && <p className={styles.service_btn} onClick={(e) => this.linkOrderDetail(e, item, 2)}>查看详情</p>}
                      </div>
                    </div>
                  </div>
                  {
                    (item.currentTimeLineNode && item.orderState === 2) && <div className={styles.list_ft} onClick={(e) => this.processBtnHandel(e, item, 1)}>
                      <p className={styles.green_point}></p>
                      <p className={styles.context}>{ item.currentTimeLineNode }</p>
                      {item.progressCode !== 30 && <p className={styles.arrow_icon}></p>}
                    </div>
                  }
                </li>
              );
            })
          }
        </ul>
        <ContactWay
          isContactWayWrapperShow={isContactWayWrapperShow}
          imUrl={imUrl}
          phoneNum={Number(phoneNum[0])}
          contactWayClose={this.contactWayClose.bind(this)}
        />
        <Confirm
          isConfirmShow={confirm.show}
          leftBtnText={confirm.leftBtnText}
          rightBtnText={confirm.rightBtnText}
          leftBtnAction={this.leftBtnAction.bind(this)}
          rightBtnAction={this.rightBtnAction.bind(this)}
          onConfirmClose={this.onConfirmClose.bind(this)}
          confirmTitle={confirm.title}
          confirmContent={confirm.content}
        />
        <LoadingMore show={this.state.isLoadMoreShow} text={this.state.loderMoreText} />
        <GoTop goTop={goTop}/>
        <Loading show={ isLoading } />
      </div>) : null
    );
  }
}